<script>
  const logger = require('heroku-logger')

export default {
    name: 'landing2',
    data () {
      return {
        blockstack: window.blockstack,
        visible: false
      }
    },
    methods: {
      signIn () {
        logger.info('Sign in to blockstack request')
        /*      Raven.captureBreadcrumb({
                message: 'User Signing In',
                category: 'action'
              }) */
        const blockstack = this.blockstack
        blockstack.redirectToSignIn(window.location.origin, window.location.origin + '/manifest.json', ['store_write', 'publish_data'])
      }
    },
    mounted () {
      logger.info('script1')
      let script = document.createElement('link')
      script.setAttribute('rel', 'stylesheet')
      script.setAttribute('href', 'assets/css/green.css')
      script.setAttribute('id', 'main_style')
      script.async = true
      script.onload = () => {
        this.visible = true
      }
      document.head.appendChild(script)
    }
  }
</script>

<style scoped>
  [v-cloak] {
    display: none;
  }
  svg:not(:root) { overflow: hidden; }
  *, ::after, ::before { box-sizing: inherit; }
  .custom_logo_header {
    background-color: transparent;
